.app {
  text-align: justify;
  color: white;
  display: flex;
  flex-direction: column;
}

.renameStateWrapper {
  position: absolute;
  left: calc(63% - 130px);
  top: calc(35% - 36px);
  width: 260px;
  border-style: solid;
  border-width: 1px;
  border-color: white;
  color: white;
  background-color: rgb(44, 44, 44);
  box-sizing: border-box;
  text-align: center;
  padding: 10px;
}

.renameStateInput {
  background-color: transparent;
  border-style: solid;
  border-width: 1px;
  border-color: white;
  color: white;
  box-sizing: border-box;
  width: 40px;
  height: 25px;
  transition-duration: 0.2s;
  text-align: center;
  margin-right: 5px;
  margin-left: 5px;
}

.renameStateLabel {
  padding-bottom: 5px;
}

#contextMenu {
  position: absolute;
  left: 0;
  top: 0;
  width: 120px;
}

.contextMenuOption {
  width: 100%;
  height: 25px;
  border-style: solid;
  border-width: 1px;
  border-color: white;
  color: white;
  background-color: rgb(44, 44, 44);
  box-sizing: border-box;
  transition-duration: 0.2s;
  text-align: left;
}

.contextMenuTextWrapper {
  width: 100%;
  height: 25px;
  display: flex;
  flex-direction: row;
}

.contextMenuTextLabel {
  width: 50%;
  border-style: solid;
  border-width: 1px;
  border-color: white;
  color: white;
  background-color: rgb(44, 44, 44);
  box-sizing: border-box;
  text-align: left;
}

.contextMenuTextInput {
  width: 50%;
  border-style: solid;
  border-width: 1px;
  border-color: white;
  color: white;
  background-color: rgb(44, 44, 44);
  box-sizing: border-box;
  transition-duration: 0.2s;
  text-align: left;
}

.simWrapper {
  justify-content: center;
  align-self: center;
  display: flex;
  flex-direction: row;
  width: 1000px;
  max-width: 90vw;
  height: 600px;
  max-height: 90vh;
}

.simWrapperLeft {
  width: 30%;
}

.simWrapperRight {
  width: 70%;
}

.txtTransitionsWrapper {
  border-style: solid;
  border-width: 1px;
  border-color: white;
  box-sizing: border-box;
  height: 60%;
  text-align: center;
  overflow: auto;
}

.transition {
  justify-content: center;
  height: 30;
  display: flex;
  flex-direction: row;
  text-align: center;
  border-style: solid;
  border-width: 1px;
  border-color: white;
  color: white;
  box-sizing: border-box;
}

.add, .remove {
  border-style: solid;
  border-width: 1px;
  border-color: white;
  color: white;
  box-sizing: border-box;
  background-color: transparent;
  transition-duration: 0.2s;
  width: 20px;
  height: 20px;
  text-align: center;
  padding: 0%;
}

.txtStartSelect {
  border-style: solid;
  border-width: 1px;
  border-color: white;
  color: white;
  box-sizing: border-box;
  background-color: transparent;
  transition-duration: 0.2s;
  width: 40%;
}

.txtAccept {
  border-style: solid;
  border-width: 1px;
  border-color: white;
  color: white;
  box-sizing: border-box;
  height: 20%;
}

.txtAcceptSelect {
  border-style: solid;
  border-width: 1px;
  border-color: white;
  color: white;
  box-sizing: border-box;
  background-color: transparent;
  transition-duration: 0.2s;
  width: 50px;
  height: 25px;
}

.node {
  cursor: move;
}

.simReset {
  border-style: solid;
  border-width: 1px;
  border-color: white;
  color: white;
  box-sizing: border-box;
  transition-duration: 0.2s;
  background-color: transparent;
  width: 50%;
  height: 10%;
  font-size: inherit;
}

.simStep {
  border-style: solid;
  border-width: 1px;
  border-color: white;
  color: white;
  box-sizing: border-box;
  transition-duration: 0.2s;
  background-color: transparent;
  width: 50%;
  height: 10%;
  font-size: inherit;
}

.simStep:hover, .simReset:hover {
  background-color: rgb(0, 100, 100);
}

.draggable {
  cursor: move;
}

.guiSim {
  display: block;
  border-style: solid;
  border-width: 1px;
  border-color: white;
  box-sizing: border-box;
  width: 100%;
  height: 80%;
  margin: 0%;
  padding: 0%;
}

.stackWrapper, .inputWrapper, .txtStartWrapper {
  display: flex;
  flex-direction: row;
  height: 10%;
}

.stackLabel, .inputLabel {
  border-style: solid;
  border-width: 1px;
  border-color: white;
  box-sizing: border-box;
  width: 30%;
}

.txtStartLabel {
  border-style: solid;
  border-width: 1px;
  border-color: white;
  box-sizing: border-box;
  width: 60%;
}

.stackState {
  border-style: solid;
  border-width: 1px;
  border-color: white;
  box-sizing: border-box;
  padding: 15px 5px;
  width: 70%;
}

.inputInput {
  background-color: transparent;
  border-style: solid;
  border-width: 1px;
  border-color: white;
  color: white;
  box-sizing: border-box;
  width: 70%;
  transition-duration: 0.2s;
}

.boxInput {
  background-color: transparent;
  border-style: solid;
  border-width: 1px;
  border-color: white;
  color: white;
  box-sizing: border-box;
  width: 25px;
  height: 25px;
  transition-duration: 0.2s;
}

.inputInput::placeholder {
  color: white;
}

.inputInput:hover, .txtStartSelect:hover, .txtAcceptSelect:hover, .add:hover, .boxInput:hover, .contextMenuOption:hover, .contextMenuTextInput:hover {
  background-color: rgb(0, 68, 0);
}

.remove:hover {
  background-color: rgb(124, 0, 0);
}

.inputInput:hover, .txtStartSelect:hover, .txtAcceptSelect:hover, .add:hover, .boxInput:hover, .contextMenuOption:hover, .contextMenuTextInput:hover {
  outline: none;
}

